import './App.css';
import { Link } from 'react-router-dom';

const Main = () =>{
    return(
        <>
        <img src="./ecomil_logo.png" className="icon"></img>
        <ul className="Link">
        <li><Link to="/Nutrition" className="inner-li">Nutrition Information</Link></li>
        <li><Link to="/Authentication" className="inner-li">Authentication Check</Link></li>
        </ul>
        </>
    )
}

export default Main