import './App.css';
import './App.scss';
import {useState,useEffect, useRef} from 'react';

const Authentication = () =>{
    const [initial,setInitial]=useState('Key in Authentication code above');
    const [name,setName]=useState();
    const [manufacture,setManufacture]=useState();
    const [expiry,setExpiry]=useState();
    const [none,setNone]=useState();
    const refContainer=useRef(null);

    const handleSubmit = (e)=>{
        e.preventDefault();
        if (refContainer.current.value==='001') {
            alert('Your product is authentic!');
            setName('Product Name: Ecomil Infant Formula Stage 2');
            setManufacture('Manufacturing date: 12 November 2021');
            setExpiry('Best before date: 12 May 2022');
            setNone();
            setInitial();
        }
        else {
            alert('Please ensure your Authentication code correctly');
            setNone('Your product is not within our database');
            setName();
            setManufacture();
            setExpiry();
            setInitial();
        }
    }
    return (
        <>
        <img src="./ecomil_logo.png" className="icon" id="icon"></img>
        <div className="input_box">
        <h2>Please key in the Authentication code found on packaging</h2>
        <div className="form__group field">
        <input type="input" className="form__field" placeholder="Authentication code" ref={refContainer} id="name" required></input>
        <label className="form__label">Authentication code</label>
        </div>
        
        <button type="submit" onClick={handleSubmit}>Submit</button>
        </div>
        
        <div className="item">
            <h2>Your product details:</h2>
            <h3>{initial}</h3>
            <h3>{name}</h3>
            <h3>{manufacture}</h3>
            <h3>{expiry}</h3>
            <h3>{none}</h3>
        </div>
        </>
    )
}

export default Authentication;